import classNames from 'classnames/bind';

import style from './ArtistCard.module.scss';
import { Artist } from '../../models/Artist';

const css = classNames.bind(style);

interface PlaylistCardProps {
  artist: Artist;
  width: number;
}

function ArtistCard({ artist, width }: PlaylistCardProps): JSX.Element {
  return (
    <div className={`${css('artist-card')}`} style={{ width }}>
      <img className={css('thumbnail')} src={artist.thumbnail || ''} alt="" />
      <p className={css('name')}>{artist.name}</p>
    </div>
  );
}
export default ArtistCard;
