import { useEffect, useState } from 'react';
import style from './Home.module.scss';
import classNames from 'classnames/bind';
import { Playlist } from '../../models/Playlist';
import playlistService from '../../services/playlist-service';
import PlaylistCard from '../../components/playlist-card/PlaylistCard';
import { Link } from 'react-router-dom';
import { Artist } from '../../models/Artist';
import ArtistCard from '../../components/artist-card/ArtistCard';
import artistService from '../../services/artist-service';

const css = classNames.bind(style);

function Home() {
  const [popularPlaylists, setPopularPlaylists] = useState<Playlist[]>();
  const [popularArtists, setPopularArtists] = useState<Artist[]>();

  useEffect(() => {
    async function getPopularPlaylists() {
      const playlist = (await playlistService.getPlaylist(
        '82850308-3be6-483a-a7fd-b96bb1f34333'
      )) as Playlist;
      const playlists: Playlist[] = [];
      for (let i = 0; i < 10; i++) {
        playlists.push(playlist);
      }
      setPopularPlaylists(playlists);
    }
    async function getPopularArtists() {
      const artist = (await artistService.getArtist(
        'a6019fda-9d7e-44b7-8168-e6656471c200'
      )) as Artist;
      const artists: Artist[] = [];
      for (let i = 0; i < 10; i++) {
        artists.push(artist);
      }
      console.log(artist);
      setPopularArtists(artists);
    }
    getPopularPlaylists();
    getPopularArtists();
  }, []);

  return (
    <div className={`${css('home')}`}>
      {popularPlaylists && (
        <section className={`${css('element')}`}>
          <h2 className={`heading-2 ${css('element-title')}`}>
            Popular Playlists
          </h2>
          <div className={`${css('element-items')}`}>
            {popularPlaylists.map((pp, index) => (
              <Link to={`/playlist/${pp.id}`} key={index}>
                <PlaylistCard playlist={pp} width={200} />
              </Link>
            ))}
          </div>
        </section>
      )}
      {popularArtists && (
        <section className={`${css('element')}`}>
          <h2 className={`heading-2 ${css('element-title')}`}>
            Popular Artists
          </h2>
          <div className={`${css('element-items')}`}>
            {popularArtists.map((a) => (
              <Link to={`/artist/${a.id}`} key={a.id}>
                <ArtistCard artist={a} width={200} />
              </Link>
            ))}
          </div>
        </section>
      )}
    </div>
  );
}

export default Home;
